

















// import {wdId} from 'vuex';
export default {
	data() {
		return {
			dc:'',
			dc_id:'',
			status:0
		};
	},
	onLoad(option) {
		var that = this;
		// 存网点id
		uni.setStorageSync('wdId',option.id)
		// that.$store.state.wdId = option.id
		uni.request({
			url:that.http+'index/getDcList',
			method:'POST',
			header:that.header,
			data:{
				net_id:option.id
			},
			success: (res) => {
				console.log(res)
				if(res.data.code==200){
					that.dc = res.data.data
					that.status = res.data.data[0].id
				}else{
					uni.showToast({
						title:res.data.mes,
						icon:'none'
					})
				}
			},
			fail() {
				uni.showToast({
					title:'网络延迟，请刷新后重试!',
					icon:'none'
				})
			}
		})
	},
	methods:{
		next(){
			var that = this;
			// 先判断是否实名
			uni.request({
				url:that.http+'index/ifIdcard',
				method:'POST',
				header:that.header,
				data:{
					user_id:uni.getStorageSync('token')
				},
				success: (res) => {
					console.log(res)
					if(res.data.data==1){
						uni.navigateTo({
							url:'../yuYue2/yuYue2?id='+that.status
						})
					}else{
						console.log(2)
						uni.navigateTo({
							url:'../realName/realName'
						})
					}
				},
				fail() {
					uni.showToast({
						title:'网络延迟，请刷新后重试!',
						icon:'none'
					})
				}
			})
		}
	}
}
